@import url(https://fonts.googleapis.com/css2?family=Lexend+Zetta&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Home-video {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  z-index: -2;
  opacity: 50%;
}

.Home-logo {
  position: relative;
  width: 500px;
  margin-top: 7%;
  z-index: -1;
}

.Home-link {
  font-size: 25px;
  font-family: 'Lexend Zetta', sans-serif;
  color: black;
}

@media only screen and (max-width: 768px) {
  .Home-video {
    display: none;
  }
  .Home-logo {
    width: 330px;
    margin-top: 25%;
  }
}

.Options-image {
  width: 13%;
  margin-bottom: 4%;
  margin-top: 5%;
}
.Options-paragraph {
  margin-bottom: 5%;
  margin-left: 6%;
  margin-right: 6%;
  font-family: 'Lexend Zetta', sans-serif;
  font-size: 15px;
}
.Options-links {
  margin-bottom: 3%;
  font-family: 'Lexend Zetta', sans-serif;
  font-size: 22px;
}
.Options-jp {
  margin-top: 6.5%;
  font-family: 'Lexend Zetta', sans-serif;
  font-size: 9px;
}

h3 {
  margin-bottom: 3%;
}

a {
  text-decoration: none !important;
  color: black;
}

.Options-icons {
  font-size: 30px;
}

@media only screen and (max-width: 768px) {
  .Options-image {
    width: 25%;
    margin-bottom: 5%;
    margin-top: 7%;
  }

  .Options-paragraph {
    font-size: 12px;
    margin-bottom: 12%;
  }
  .Options-icons {
    margin-top: 5%;
    font-size: 29px;
    margin-bottom: 12%;
  }
}

.Work-title {
  font-family: 'Lexend Zetta', sans-serif;
  font-size: 40px;
}

.Work-image {
  width: 13%;
  margin-bottom: 4%;
  margin-top: 5%;
}

.Work-videos {
  margin-right: 5%;
  margin-top: 3%;
}

iframe {
  margin-left: 3%;
  margin-bottom: 3%;
}

.Work-icon {
  font-size: 18px;
  font-family: 'Lexend Zetta', sans-serif;
}

.Work-videos-title {
  margin-right: 72%;
  font-family: 'Lexend Zetta', sans-serif;
  font-size: 22px;
  margin-top: 3%;
}

.Work-photography-title {
  margin-right: 65%;
  font-family: 'Lexend Zetta', sans-serif;
  font-size: 22px;
  margin-top: 4%;
}

.Work-photography {
  display: inline-block;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.Work-jp {
  margin-top: 2.5%;
  margin-bottom: 1%;
  font-family: 'Lexend Zetta', sans-serif;
  font-size: 9px;
}

@media only screen and (max-width: 768px) {
  .Work-image {
    width: 18%;
  }
  .Work-title {
    margin-top: 7%;
    margin-bottom: 4%;
  }
  iframe {
    align-items: center !important;
    margin-bottom: 5%;
    width: 75%;
  }
  .Work-photography {
    margin-right: 0% !important;
    margin-left: 0% !important;
    margin-top: 7%;
    margin-bottom: 7%;
  }

  .Work-icon {
    margin-bottom: 6%;
    font-size: 15px !important;
  }
}

.Photo-img {
  width: 75%;
  margin-bottom: 3%;
}

